/* Resources */
.navbar-button {
  display: block;
  width: 60px;
  height: 64px;
}
.navbar-button:hover {
    background: rgba(255, 255, 255, 0.25);
}
#navbar-identity {
  position: absolute;
  top: 0;
  right: 0;
  background-size: contain;
}
#navbar-identity.identity-anon {
    background-image: url(assets/identity/robot.png);
}
#navbar-identity.identity-citizen {
    background-image: url(assets/identity/human.png);
}



