/* Resources */
/* Main */
body {
  font-size: 16px;
}
button, input, optgroup, select, textarea {
  font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif;
}
button:focus, input:focus, optgroup:focus, select:focus, textarea:focus {
    outline: none;
}
h1, h2, h3, h4, h5 {
  font-family: "century-std", "noto-serif-patch", "source-han-serif-tc", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: bold;
  padding: 0;
  margin: 0;
}
h1 {
  font-size: 3rem;
}
h1.super {
    font-size: 6rem;
}
h1.small {
    font-size: 2rem;
    font-weight: bold;
}
h1.sit {
    text-align: center;
    margin-top: 4rem;
    margin-bottom: 0.5rem;
}
h2 {
  font-size: 2.25rem;
}
h2.small {
    font-size: 1.5rem;
    font-weight: bold;
}
hgroup > h2 {
  font-size: 1.5rem;
}
h3 {
  font-size: 1.875rem;
}
h3.small {
    font-size: 1.25rem;
    font-weight: bold;
}
h4 {
  font-size: 1.5rem;
}
h4.small {
    font-size: 1.125rem;
    font-weight: bold;
}
h5 {
  font-size: 1.125rem;
}
h5.small {
    font-size: 1rem;
    font-weight: bold;
}
label {
  margin: 0;
}
img {
  display: block;
}
a, a:hover, a:focus, a:active, a:visited {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}
a.a-text,
.a-text-only a {
  display: inline;
  border-bottom: 2px rgba(80, 227, 194, 0.5) solid;
}
a.a-text:hover, a.a-text:focus, a.a-text:active,
  .a-text-only a:hover,
  .a-text-only a:focus,
  .a-text-only a:active {
    border-bottom: 2px #50e3c2 solid;
}
a.a-block {
  display: block;
}
a.a-block .a-target {
    border-bottom: 2px rgba(80, 227, 194, 0.5) solid;
}
a.a-block:hover .a-target, a.a-block:focus .a-target, a.a-block:active .a-target {
    border-bottom: 2px #50e3c2 solid;
}
.close {
  float: none;
  display: block;
  position: absolute;
  top: -0.5rem;
  right: -2px;
  font-size: 2rem;
  line-height: 1;
  border-bottom-width: 4px;
  font-weight: normal;
  text-shadow: none;
  cursor: pointer;
}
.close, .close:hover, .close:focus {
    color: inherit;
    opacity: 1;
}
.list-unstyled {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}
.navbar,
.navbar-brand {
  margin: 0;
  padding: 0;
}
input[type='text']:not([class^="el"]),
input[type='email']:not([class^="el"]),
input[type='password']:not([class^="el"]) {
  background: none;
  border: none;
  border-bottom: 2px #9b9b9b solid;
  line-height: 1.75;
  padding: 0;
  margin: 0;
}
input[type='text']:not([class^="el"]).full-width,
  input[type='email']:not([class^="el"]).full-width,
  input[type='password']:not([class^="el"]).full-width {
    width: 100%;
}
button:not([class^="el"]):not(.btn-prev):not(.btn-next) {
  background: #b9b9b9;
  border: 2px #9b9b9b solid;
  border-radius: 2px;
  line-height: 1;
  padding: 0.75rem;
  cursor: pointer;
}
button:not([class^="el"]):not(.btn-prev):not(.btn-next).small {
    font-size: 0.875rem;
    padding: 0.5rem;
}
input + button:not([class^="el"]) {
  margin-left: 0.5rem;
}
.field {
  margin: 0.5rem 0;
}
.field:first-of-type {
    margin: 0 0 0.5rem;
}
.field:last-of-type {
    margin: 0.5rem 0 0;
}
input[type='checkbox']:not([class^="el"]) {
  -webkit-appearance: none;
  cursor: pointer;
  height: 1.5rem;
  width: 1.5rem;
  position: relative;
  -webkit-transition: .15s;
  background-color: rgba(185, 185, 185, 0.25);
  border: 2px #9b9b9b solid;
  border-radius: 2px;
}
input[type='checkbox']:not([class^="el"]):checked {
    background-color: #b9b9b9;
}
input[type='checkbox']:not([class^="el"]):checked:before {
      content: '';
      display: block;
      width: 8px;
      height: 14px;
      border-bottom: 2px black solid;
      border-right: 2px black solid;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -65%) rotate(45deg);
              transform: translate(-50%, -65%) rotate(45deg);
}
.form-check-label > * {
  vertical-align: middle;
}
.form-check-label > input + span {
  margin-left: 0.25rem;
}
.accordion > .card {
  background: none;
  border: none;
  border-radius: 0;
  padding: 0 1rem;
}
.accordion > .card > .card-head {
    padding: 1rem 0;
}
.accordion > .card > .card-body > .padding {
    padding-bottom: 1rem;
}
.modal-mask {
  display: table;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.92);
  transition: opacity .3s ease;
}
.modal-mask > .modal-wrapper {
    display: table-cell;
    vertical-align: middle;
}
.modal-mask > .modal-wrapper > .modal-dialog {
      width: 18rem;
      margin: 0 auto;
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.25);
      transition: all .3s ease;
}
.modal-enter {
  opacity: 0;
}
.modal-enter .modal-dialog {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}
.modal-leave-active {
  opacity: 0;
}
.modal-leave-active .modal-dialog {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}
.text-color-park {
  color: #FFB55D;
}
.bg-watchout {
  background-color: #50E3C2;
}
.bg-park {
  background-color: #FFB55D;
}
.bg-park-light {
  background-color: rgba(255, 181, 93, 0.25);
}
.bg-lab {
  background-color: #50E3C2;
}
.bg-lab-light {
  background-color: rgba(80, 227, 194, 0.25);
}
.bg-ask {
  background-color: #4DEBDF;
}
.bg-ask-light {
  background-color: rgba(77, 235, 223, 0.25);
}
.bg-ask-gradient-to-transparent {
  background: linear-gradient(to bottom, rgba(77, 235, 223, 0.25), rgba(77, 235, 223, 0));
}
.bg-musou {
  background-color: #FF5368;
}
.bg-musou-light {
  background-color: rgba(255, 83, 104, 0.25);
}
.bg-edu {
  background-color: #B7F661;
}
.bg-edu-light {
  background-color: rgba(183, 246, 97, 0.25);
}
.header-center {
  text-align: center;
}
.header-block {
  margin: 0;
  padding: 0.333333em 0;
}
.header-block-tall {
  padding: 1em 0;
}
section {
  width: 100%;
  padding-bottom: 2rem;
}
.container-960 {
  max-width: 990px;
  margin-left: auto;
  margin-right: auto;
}
.max-width-960 {
  max-width: 960px;
}
.logo-type {
  display: block;
  height: auto;
}
.logo-small, .icon-small {
  width: 24px;
  height: 24px;
  background-size: contain;
  background-position: center center;
}
.logo-small.musou {
  background-image: url(https://watchout.tw/asset/logo/musou/small.png);
}
.logo-small.woo {
  background-image: url(https://watchout.tw/asset/logo/woo/small.png);
}
.icon-small.transcript {
  background-image: url(https://watchout.tw/asset/icon/transcript/small.png);
}
.pgroup {
  width: 18rem;
}
.pgroup-center {
  margin: 0 auto;
}
#pitcher {
  position: fixed;
  bottom: 0.25rem;
  right: 0.25rem;
  z-index: 2;
}
#pitcher > .button {
    display: block;
    width: 5.625rem;
    height: 5.625rem;
    border-radius: 50%;
    background-size: contain;
    background-image: url(https://watchout.tw/asset/pitcher.png);
}
#pitcher > .close {
    top: auto;
    bottom: -0.25rem;
    color: #50E3C2;
    opacity: 0.75;
}



